<template>
    <div class="role-manager-box">
        <h2 v-if="this.$route.params.key == 'add'">角色管理</h2>
        <h2 v-if="this.$route.params.key == 'modify'">修改角色</h2>
        <h2 v-if="this.$route.params.key == 'look'">查看角色</h2>
        <div class="content-div">
          <div>
            <div>
              <span>角色名称</span>
              <input type="text" v-model="roleName">
            </div>
            <div>
                <span>角色说明</span>
                <textarea name="" id="" cols="30" rows="10" v-model="roleDescription"></textarea>
            </div>
            <div>
                <span>角色权限</span>
                <span class="permission-specification">
                    <el-tree
                        :data="data2"
                        show-checkbox
                        node-key="id"
                        ref="tree"
                        :default-expanded-keys="[1,2, 3,4]"
                        :default-checked-keys="selectedPermission"
                        :props="defaultProps">
                    </el-tree>
                </span>
            </div>
            <div>
                <span>标准价格</span>
                <input type="text" v-model="standardPrice">
                <!-- <el-button type="primary" @click="getPrice()">获取价格</el-button> -->
            </div>
            <div>
                <span>续费价格</span>
                <input type="text" v-model="renewPrice">
            </div>
          </div>
        </div>
        <div class="btns">
             <el-button type="primary" @click="addRole()" v-if="this.$route.params.key == 'add'">确定</el-button>
              <el-button type="primary" @click="modifyRole()" v-if="this.$route.params.key == 'modify'">确定</el-button> 
             <el-button type="info" @click="goBack()">取消</el-button>
        </div>
    </div>
</template>
<script>
import Axios from 'axios'
import { javaServiceUrl } from './../../util/constant.js'

// import { setStorge,getStorge } from './../../util/localstorage.js'
// import { getStorge } from '@/util/localstorage'

export default {
    data(){
        return{
        data2: [],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        roleName:"",//角色名称
        roleDescription:"",//角色说明
        standardPrice:0,//标准价格
        renewPrice:0,//续费价格
        state:"0",//状态
        selectedPermission:[],//选中的权限
        getPermissionses:{},//获取到的权限
        updateSelectedPermission:[],//要修改的权限
        allPermission:{},
        authorization:"",
        token:"",
        }
    },
      created(){
  
        var storage = window.localStorage;
        this.authorization = storage.getItem("userId");
        this.token = storage.getItem("token");

    },
    mounted(){
      if(this.$route.params.key != 'add'){
        this.modify = this.$route.params.row;
        this.roleName=this.modify.roleName
        this.roleDescription=this.modify.roleDescription
        this.standardPrice=this.modify.standardPrice
        this.renewPrice=this.modify.renewPrice
        this.state=this.modify.state
        this.id=this.modify.id
        this.allPermission = this.modify.permissions;
        this.data2 = this.formatData(this.modify.permissions);
      }else{
        this.getPermissions();
      }
    
    },
  
    methods:{
      //取消
        goBack(){
            this.$router.go(-1);
        },
  
      //重组数据结构
      formatData(data) {
        let newArr = [];
        let idarr = ['AShare_access_matrix', 'Sentiment_access_matrix', 'ccs_e_commerce_matrix', 'ccs_live_matrix', 'ccs_other_matrix'];
        let id = 0;
        for(let key in data) {
        
            
			let obj = {
			  id: id+=1,
			  label: key,
			  children: []
			}
			let singleArr = data[key];
			singleArr.forEach(item => {
			  //为了编辑页面回显选中的权限
			  if(item.state == "unlock"){
				  this.selectedPermission.push(item.industry_id || item.enterprise_id || item.id);
			  }
			  obj.children.push({
				id: item.industry_id || item.enterprise_id || item.id,
				label: item.industry_name || item.enterprise_name || item.name,
			  })
			  for(let i = 0; i < item.length; i++){
			  }
			});
			newArr.push(obj);
          
        }
        return newArr;
      },
      //获取所有权限
      getPermissions(){
          Axios({
               url: javaServiceUrl+'/userPermission/getPermissions',
               method: 'get',
               headers: {"Authorization":"user-" + this.authorization + "-" +this.token},     
              
            })
            .then(res=>{
                if(res.data.msg){
                    this.$router.push({
                        name:'login',
                    })
                }else{
                  this.allPermission = res.data;
                  this.data2 = this.formatData(res.data);
                }
              
            })
            .catch(err=>{
                console.log(err)
            })
      },
      //新增角色
      addRole(){
          this.getCheckedNodes();
          Axios({
               url: javaServiceUrl + '/userPermission/insertOrUpdateRole',
               method: 'post',
               headers: {"Authorization":"user-" + this.authorization + "-" +this.token},     
               data:{
                  roleName:this.roleName,
                  roleDescription:this.roleDescription,
                  standardPrice:this.standardPrice,
                  renewPrice:this.renewPrice,
                  state:this.state,
                  permissions:this.allPermission,
               }
              
            })
            .then(res=>{
                 if(res.data.msg){
                    this.$router.push({
                        name:'login',
                    })
                }else{
                  this.$router.push({name: 'roleManagement'})
                }
            })
            .catch(err=>{
                console.log(err)
            })
      },
      //修改角色
      modifyRole(){
          this.getCheckedNodes();
          Axios({
               url: javaServiceUrl + '/userPermission/insertOrUpdateRole',
               method: 'post',
               headers: {"Authorization":"user-" + this.authorization + "-" +this.token}, 
               data:{
                  roleName:this.roleName,
                  roleDescription:this.roleDescription,
                  standardPrice:this.standardPrice,
                  renewPrice:this.renewPrice,
                  state:this.state,
                  permissions:this.allPermission,
                  id:this.id
               }
              
            })
            .then(res=>{
              
                   if(res.data.msg){
                    this.$router.push({
                        name:'login',
                    })
                }else{
                  this.$router.push({name: 'roleManagement'})
                }
            })
            .catch(err=>{
                console.log(err)
            })
      },

      //获取选中节点
      getCheckedNodes() {
        this.updateSelectedPermission = this.$refs.tree.getCheckedNodes();
        //选中的值会写到原始数据中，传给后台
        let temp = [];
        let sentimentTemp = 0;
        for(let i = 0; i < this.updateSelectedPermission.length;i++){
          let nodeId = this.updateSelectedPermission[i].id;
          let nodeName = this.updateSelectedPermission[i].label;
          

          for(let key in this.allPermission) {
            let singleArr = this.allPermission[key];
            if(key == "AShare_access_matrix"){
                for(let i = 0; i < singleArr.length;i++) {
                  if(singleArr[i].industry_id == nodeId){
                      singleArr[i].state = "unlock";
                      for(let j = 0; j < singleArr[i].enterprises.length;j++){
                          singleArr[i].enterprises[j].state = "unlock";
                          
                      }
                      temp.push(nodeId);
                  }else{
                    let ststeFlag = false;
                    for (var f1 in temp) {
                      if (temp[f1].indexOf(singleArr[i].industry_id) > -1) {
                        ststeFlag = true;
                      }
                    }
                    if(!ststeFlag){
                      singleArr[i].state = "lock";
                      for(let j = 0; j < singleArr[i].enterprises.length;j++){
                          singleArr[i].enterprises[j].state = "lock";
                      }
                    }
                  }
                }
            }else if(key == "Sentiment_access_matrix"){
				for(let i = 0; i < singleArr.length;i++) {
                  if(singleArr[i].id == nodeId){
                      singleArr[i].state = "unlock";
                      
                      temp.push(nodeId);
                  }else{
                    let ststeFlag = false;
                    for (var f1 in temp) {
                      if (temp[f1].indexOf(singleArr[i].id) > -1) {
                        ststeFlag = true;
                      }
                    }
                    if(!ststeFlag){
                      singleArr[i].state = "lock";
                    
                    }
                  }
                }
            }else{
              
              for(let i = 0; i < singleArr.length;i++) {
                
                if(nodeId == singleArr[i].enterprise_id){
                  singleArr[i].state = "unlock";
                  temp.push(nodeId);
                }else{
                  let ststeFlag = false;
                  for (var f1 in temp) {
                    if (temp[f1].indexOf(singleArr[i].enterprise_id) > -1) {
                      ststeFlag = true;
                    }
                  }
                  if(!ststeFlag){
                    singleArr[i].state = "lock";
                  }
                  
                }
              }
            }
          }
        }
      }
      
        
    }

}
</script>
<style lang="scss" scoped>
.role-manager-box{
    background: #fff;
    padding: 20px;
    h2{
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .content-div,.btns{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    div{
        margin: 10px 20px;
        input{
            width: 300px;
            height: 40px;
            border: 1px solid #DCDFE6;
            border-radius: 4px;
        }
        .permission-specification{
            display: inline-block;
            width: 300px;
            height: 300px;
            overflow:auto;
            border: 1px solid #DCDFE6;
            vertical-align: text-top;
        }
    }
    textarea{
        resize: none;
        width: 300px;
        height: 100px;
        vertical-align: text-top;
        border:1px solid #DCDFE6;
    }
}
</style>




